import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import News from '../../components/news';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
	isLoading: false,
};

@observer
class NewsList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadNews() {
		this.setState({ isLoading: true });
		const { newsStore } = this.props.stores;
		await newsStore.load();
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadNews();
	}

	render() {
		const { newsStore } = this.props.stores;
		const { newsList } = newsStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row'>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5><Text id='news.heading'>Siste nytt</Text></h5>
						</div>
						{newsList && newsList.map(n => <News stores={this.props.stores} news={n} />)}
					</div>
				</div>
			</div>
		);
	}
}

export default NewsList;
